@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src: url("../fonts/#{$url}.eot"),
    url("../fonts/#{$url}.woff") format("woff"), 
    url("../fonts/#{$url}.ttf") format("truetype"); 
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}